@import "../../../styles/variables.scss" ;

.navbar-section-dark {
    display: flex;
    position: fixed;
    width: 100%;
    justify-content: space-between;
    padding: 40px 60px;
    color: white;
    font-family: $police;
    animation: from-top 2s;
    animation-iteration-count: 1;
    background-color: #1b1919;

    .navbar-section-items {
        display: flex;
        justify-content: space-between;

        & h1 {
            cursor: pointer;
        }   

        & p {
            padding-top: 18px;
            padding-left: 80px;

            &:hover {
                text-decoration: underline;
            }
        }
        & button {
            background-color: #322c2c;
            border: none;
            cursor: pointer;
            display: inline;
            padding: 0 5px;
            margin: 0;
            width: 0;
            height: 0;
            font-size: 15px;
            color: white;
        }
    }

    .navbar-section-icons {
        display: flex;
        justify-content: space-between;
    }

    .navbar-section-icons-childs {
        padding-top: 18px;
        padding-right: 50px;

        #navbar-icon {
            color: white;

            &:hover {
                color: $main-color;
                transition-duration: 0.7;
                cursor: pointer;
            }
        }
    }
    .navbar-section-icons-childs button {
        background-color: #1b1919;
        border: none
            
    }
}

.navbar-section-light {
    @extend .navbar-section-dark;

    background-color: $light-background;

    .navbar-section-items {
        display: flex;
        justify-content: space-between;
        background-color: $light-background;

        & h1 {
            cursor: pointer;
            color: black;
            background-color: $light-background;
        }

        & p {
            padding-top: 18px;
            padding-left: 80px;
            background-color: $light-background;

            &:hover {
                text-decoration: underline;
            }
        }

        & button {
            background-color: #322c2c;
            border: none;
            cursor: pointer;
            display: inline;
            padding: 0 5px;
            margin: 0;
            width: 0;
            height: 0;
            font-size: 15px;
        }
    }
    
    .navbar-section-icons {
        display: flex;
        justify-content: space-between;
        background-color: $light-background;
    }

    .navbar-section-icons-childs {
        padding-top: 18px;
        padding-right: 50px;
        background-color: $light-background;

        #navbar-icon {
            color: black;
            background-color: $light-background;

            &:hover {
                color: $main-color;
                transition-duration: 0.7;
                cursor: pointer;
            }
        }
    }

    .navbar-section-icons-childs button {
        background-color: $light-background;
        border: none
            
    }
}


@keyframes from-top {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0%);
    }
}