@font-face {
    font-family: 'Poppins';
    src: url("../../assets/fonts/Poppins-Regular.ttf") format('truetype');
}

.sound-player {
    padding: 0 50px;
    bottom: 0;
    right: 0;
    width: 100%;
    position: fixed;
    color: white;
    text-align: center;
    font-family: Poppins;
    /*background-color: #322c2c;*/
    background-color: #322c2c;
    display: flex;
    gap: 30px;
    animation: from-bottom 2s;
    animation-iteration-count: 1;
    
    
}

.button-play{
    background-color: #322c2c;
    color: white;
    border-radius: 50%;
    margin: 0px;
    border: none;
    cursor: pointer;
    margin-right: 15px;
}


/* Texte d√©filant */
.messagedefilant {
    display: block;
    padding: 10px 0;
    overflow: hidden;
    position: relative;
    width: 100%;
    max-width: 300px;
    height: 40px;
    background-color: #322c2c;
    border-radius: 50%;
  }
   
  .messagedefilant div {
    font-size: 18px;
    position: absolute;
    min-width: 100%; /* au minimum la largeur du conteneur */
    
  }
   
  @keyframes from-bottom {
    from {
        transform: translateY(100%);
    }

    to {
        transform: translateY(0%);
    }
}